import { App } from 'vue'
// 导入ui的基础样式
import 'element-plus/theme-chalk/base.css'
// 导入组件
import {
  ElButton,
  ElForm,
  ElFormItem,
  ElInput,
  ElRadio,
  ElTabs,
  ElTabPane
} from 'element-plus'
const components = [
  ElButton,
  ElForm,
  ElFormItem,
  ElInput,
  ElRadio,
  ElTabs,
  ElTabPane
]
// 使用一个函数将导入组件挨个注册
export default function (app :App):void {
  for (const component of components) {
    app.component(component.name, component)
  }
}
